export default {
	data() {
		return {
			activity_id: 0,
			com: '',
			name: '',
			position: '',
			phone: '',
			mailbox: ''
		}
	},
	methods: {
		apply() {
			let baseUrl = this.WebUrl
			let that = this
			if (that.com === '') {
				uni.showModal({
					title: '单位名不能为空',
					showCancel: false
				})
			}else if (that.name === '') {
				uni.showModal({
					title: '姓名不能为空',
					showCancel: false
				})
			}else if (that.position === '') {
				uni.showModal({
					title: '职位不能为空',
					showCancel: false
				})
			}else if (!(/^1[3456789]\d{9}$/.test(this.phone))) {
				uni.showModal({
					title: '联系方式输入错误',
					showCancel: false
				})
			}else if (that.mailbox === '') {
				uni.showModal({
					title: '邮箱不能为空',
					showCancel: false
				})
			}
			uni.getStorage({
				key: 'u_id',
				success(res) {
					uni.request({
						url: baseUrl + '/apply',
						method: 'POST',
						data: {
							u_id: res.data,
							activity_id: that.activity_id,
							com_name: that.com,
							name: that.name,
							position: that.position,
							phone: that.phone,
							mailbox: that.mailbox
						},
						success(res) {
							if(res.data.msg === "请求成功!") {
								uni.showModal({
									title: '报名成功',
									showCancel:false,
									success(resd) {
										if(resd.confirm){
											uni.navigateBack({
												delta: 1
											})
										}
									}
								})
							}
						}
					})
				}
			})
		},
		
		comValue(e) {
			this.com = e.target.value
		},
		
		nameValue(e) {
			this.name = e.target.value
		},
		
		positionValue(e) {
			this.position = e.target.value
		},
		
		phoneValue(e) {
			this.phone = e.target.value
		},
		
		mailboxValue(e) {
			this.mailbox = e.target.value
		},
	},
	onLoad:function(option) {
		this.activity_id = option.activity_id
	}
}
